* {
  font-family:  Helvetica, Arial, sans-serif;
}
body {
  padding: 60px;
}
.card-grid{
  display: flex;
 flex-direction: row;
 flex-wrap: wrap;
}
.input-img{
  width: 100%;
  padding: 10px;
}
.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  text-align: center;
  padding: 5px;
}
.card img{
  max-height: 170px;
}